import { FC, createElement } from 'react'
import { UrlLink } from 'src/model/person-model'

export const ThemeWebSiteLink: FC<{ webSite?: UrlLink; wrapperTag?: string }> = (props) => {
	const { webSite, wrapperTag = 'span' } = props
	if (!webSite) {
		return <></>
	}

	return createElement(wrapperTag, [], 'w: ', createElement('a', { href: webSite.url }, webSite.text))
}
